$main_color: rgb(103,172,91);
$second_color: rgb(215,216,220);

.alert {
    display: none;
    position: fixed;
    font-size: 1.5em;
    padding: 30px 100px;
    background: #fff;
    border: 5px solid $main_color;
    border-radius: 10px;
    top: 50%;
    left: 50%;
    z-index: 2;
    transform: translate(-50%, -50%); }

.title {
    font-size: 2.5em;
    font-weight: 700;
    margin-bottom: 50px; }



@media ( max-width: 1150px ) {
    .alert {
        padding: 30px 80px; } }

@media ( max-width: 994px ) {
    .alert {
        font-size: 1.1em; } }

@media ( max-width: 900px ) {
    .alert {
        padding: 30px 50px;
        font-size: 0.9em; }
    .title {
        font-size: 2em; } }

@media ( max-width: 787px ) {
    .title {
        font-size: 1.5em; } }

@media ( max-width: 640px ) {
    .alert {
        padding: 30px 30px;
        font-size: 0.8em; }
    .title {
        font-size: 1.2em;
        margin-bottom: 30px; } }

@media ( max-width: 520px ) {
    .alert {
        padding: 20px 20px;
        font-size: 0.8em; } }

@media ( max-width: 460px ) {
    .alert {
        padding: 20px 20px;
        font-size: 0.7em; } }

@media ( max-width: 400px ) {
    .title {
        font-size: 1em; } }
